<template>
<div>
<table border="1px">
<tr><th>ID</th><th>Название</th><th>Edit</th><th>Delete</th></tr>
<tr v-for="sub in this.substations" :key="sub.id"><td>{{sub.id}}</td><td>{{sub.name}}</td><td>edit</td><td>remove</td></tr>
</table>
 <input type="text" placeholder="Substation name" v-model="name" :name="name"/>
  <input type="button" value="Add" @click="save" />
</div>
</template>

<script>

var  substations = [];
export default {
  name: 'SubsTable',
  props: ['axios'],
  data(){
    return{
      substationApi: 'http://localhost:8888/subs',
      substations: [],
      name: ''
    }
  },
    created: function(){
 
	this.axios.get(this.substationApi).then(result =>
	this.substations = result.data
	)
  this.substations = substations
  console.log(this.substations)
},
    methods: {
        save: function() {
          var substation = {name: this.name}
                    this.axios.post(this.substationApi, substation).then(result =>
                    this.substations.push(result.data)
                    )
                    console.log(this.substations)
        }
    }
 }

</script>

